          ></window-one>
          <window-two
            :my-data="accept"
            title="质量验收"
            :link="
              `${link}&plate=zlys`
            "
            :window-size="windowSize"
          ></window-two>
          <window-three
            :my-data="reform"
            title="项目整改"
            :link="
              `${link}&plate=xmzg`
            "
            :window-size="windowSize"
          ></window-three>
        </div>
        <div class="middle">
          <window-four
            :my-data="aq"
            title="安全"
            :link="
              `${link}&plate=aq`
            "
            :window-size="windowSize"
            :color="['#FFB200', '#FB8D00']"
          ></window-four>
          <window-four
            :my-data="zl"
            title="技术质量"
            :link="
              `${link}&plate=jszl`
            "
