  </div>
</template>

<script>
// @ is an alias to /src
import myHeader from "@/components/myHeader";
import windowOne from "@/components/windowOne";
import windowTwo from "@/components/windowTwo";
import windowThree from "@/components/windowThree";
import windowFour from "@/components/windowFour";
import windowFive from "@/components/windowFive";
import windowSix from "@/components/windowSix";
import windowSeven from "@/components/windowSeven";
import windowEight from "@/components/windowEight";
import windowNine from "@/components/windowNine";
import windowTen from "@/components/windowTen";
import windowEleven from "@/components/windowEleven";
import Masker from "@/components/masker";
export default {
  name: "Home",
  components: {
    myHeader,
    windowOne,
    windowTwo,
    windowThree,
    windowFour,
    windowFive,
    windowSix,
    windowSeven,
    windowEight,
    windowNine,
    windowTen,
    windowEleven,
    Masker,
  },
  data() {
    return {
      progress: null,
      accept: null,
      reform: null,
      checkin: null,
      entry: null,
      register: null,
      sc: null,
      aq: null,
      zl: null,
      windowSize: {
        width: window.screen.availWidth,
        height: window.screen.availHeight,
      },
      company: "总公司",
      cascaderShow: false,
      company_type: 2,
      company_id: "list",
      companyOptions: [
        {
          value: {
            type: 2,
            company_id: "list",
            label: "总公司",
          },
          label: "总公司",
          leaf: true,
        },
        {
          value: {
            type: 3,
            company_id: "list",
            label: "分公司",
          },
          label: "分公司",
        },
        {
          value: {
            type: 4,
            company_id: "list",
            label: "项目部",
          },
          label: "项目部",
        },
      ],
      cascaderProps: {
        expandTrigger: "hover",
        lazy: true,
        lazyLoad: (node, resolve) => {
          const { value, label, level, isLeaf } = node;
          if (!isLeaf) {
            this.$axios
              .get("/api/temporary/specs/formation", {
                params: {
                  company_id: value.company_id,
                },
              })
              .then((res) => {
                const { data, code } = res.data;
                if (code == 0) {
                  const nodes = data.map((v) => {
                    return {
                      value: {
                        type: value.type,
                        company_id: v.id,
                        label: v.name,
                      },
                      label: v.name,
                      leaf: value.type - 2 == level,
                    };
                  });
                  resolve(nodes);
                }
              });
          } else {
            resolve([]);
          }
        },
      },
      showDate: "08",
      dateStr: "2020-08",
      startDate: "2020-07-26",
      endDate: "2020-08-25",
    }
  },
  computed:{
    link(){
      //${link}
      const originLink = 'http://proxy.zhongnangroup.cn/fr/decision/view/report';
      const {startDate,endDate,company_id} = this;
      switch(this.company_type){
        case 2:
          //总公司级
          return `${originLink}?viewlet=7设计稿/王雪莉/规范考核/检查报表_区域公司.cpt&ref_t=design&ref_c=45577d7d-16d4-4062-8c9d-3b8befdddcb6&industrial=建筑&startdate=${startDate}&enddate=${endDate}`
        case 3:
          //分公司级
          return `${originLink}?viewlet=/7设计稿/王雪莉/规范考核/检查报表_项目.cpt&company_id=${company_id}&startdate=${startDate}&enddate=${endDate}`
        case 4:
          //项目级
          return ``
          // return `${originLink}&__parameters__={"__pi__":true,"company_id":${company_id},"startdate":${startDate},"enddate":${endDate}}`
      }
    }
  },
  methods: {
    companyChange(value) {
      const chouse = value[value.length - 1];
      this.company_id = chouse.company_id;
      this.company_type = chouse.type;
      this.company = chouse.label;
      this.cascaderShow = false;
      if(chouse.type == 4){
        this.getProjectData();
      } else {
        this.getData();
      }
    },
    dateChange(date) {
      this.dateStr = this.formatDate(date, "yyyy-MM");
      this.endDate = this.getEndDate(date);
      this.startDate = this.getStartDate(date);
      this.getData();
    },
    getNowDate() {
      const date = new Date();
      const day = date.getDate();
      if (day > 25) {
        this.dateStr = this.formatDate(date, "yyyy-MM");
        this.endDate = this.getEndDate(date);
        this.startDate = this.getStartDate(date);
      } else {
        date.setMonth(date.getMonth() - 1);
        this.dateStr = this.formatDate(date, "yyyy-MM");
        this.endDate = this.getEndDate(date);
        this.startDate = this.getStartDate(date);
      }
    },
    getEndDate(date) {
      //当月25号
      date.setDate(25);
      return this.formatDate(date);
    },
    getStartDate(date) {
      //前月26号
      date.setMonth(date.getMonth() - 1);
      date.setDate(26);
      return this.formatDate(date);
    },
    formatDate(date, format = "yyyy-MM-dd") {
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();
      switch (format) {
        case "yyyy-MM-dd": //2020-08-25
          return [year, month, day].map(this.formatNum).join("-");
        case "yyyy-MM": //2020-08
          return [year, month].map(this.formatNum).join("-");
      }
    },
    formatNum(num) {
      return num < 10 ? `0${num}` : num;
    },
    getData() {
      const axios = this.$axios;
      const params = {
        date: this.dateStr,
        company_id: this.company_id,
      };
      const http = [
        {
          api: "/api/temporary/specs/progress",
          target: "progress",
        },
        {
          api: "/api/temporary/specs/accept",
          target: "accept",
        },
        {
          api: "/api/temporary/specs/reform",
          target: "reform",
        },
        {
          api: "/api/temporary/specs/checkin",
          target: "checkin",
        },
        {
          api: "/api/temporary/specs/entry",
          target: "entry",
        },
        {
          api: "/api/temporary/specs/register",
          target: "register",
        },
        {
          api: "/api/temporary/specs/sc",
          target: "sc",
        },
        {
          api: "/api/temporary/specs/aq",
          target: "aq",
        },
        {
          api: "/api/temporary/specs/zl",
          target: "zl",
        },
      ];
      for (const iterator of http) {
        this[iterator.target] = null;
        axios
          .get(iterator.api, {
            params,
          })
          .then((res) => {
            // console.log(res);
            const { code, data } = res.data;
            if (code == 0) {
              this[iterator.target] = data;
            }
          });
      }
    },
    getProjectData(){
      const axios = this.$axios;
      const params = {
        date: this.dateStr,
        project_id: this.company_id,
      };
      const http = [
        {
          api: "/api/temporary/specs/project/progress",
          target: "progress",
        },
        {
          api: "/api/temporary/specs/project/accept",
          target: "accept",
        },
        {
          api: "/api/temporary/specs/project/reform",
          target: "reform",
        },
        {
          api: "/api/temporary/specs/project/entry",
          target: "entry",
        },
        {
          api: "/api/temporary/specs/project/checkin",
          target: "checkin",
        },
        {
          api: "/api/temporary/specs/project/register",
          target: "register",
        },
        {
          api: "/api/temporary/specs/project/sc",
          target: "sc",
        },
        {
          api: "/api/temporary/specs/project/aq",
          target: "aq",
        },
        {
          api: "/api/temporary/specs/project/zl",
          target: "zl",
        },
      ];
      for (const iterator of http) {
        this[iterator.target] = null;
        axios
          .get(iterator.api, {
            params,
          })
          .then((res) => {
            // console.log(res);
            const { code, data } = res.data;
            if (code == 0) {
              this[iterator.target] = data;
            }
          });
      }
    }
  },
  mounted() {
    this.getNowDate();
    this.getData();
  },
};
</script>
