<template>
  <div class="module" v-loading="!myData">
    <module-name :title="title" :link="link"></module-name>
    <template v-if="myData">
      <div class="main">
        <div class="data">
          <p class="member"><span>实际验收</span>{{ myData.done_num }}</p>
          <p class="member"><span>计划验收</span>{{ myData.num }}</p>
        </div>
        <a-progress
          type="circle"
          :stroke-color="{
            '0%': '#00C4FF',
            '100%': '#009AFB',
          }"
          :width="windowSize.height * 0.1"
          :strokeWidth="14"
          :percent="myData.done_rate"
          status="normal"
          gapPosition="bottom"
        >
          <template v-slot:format="percent">
            <p class="progress-percent">{{ percent }}<span>%</span></p>
          </template>
        </a-progress>
      </div>
      <div class="chart">
        <div :id="chartId" style="width: 100%;height: 100%;"></div>
      </div>
    </template>
  </div>
</template>

<script>
import moduleName from "@/components/moduleName";
export default {
  name: "windowSeven",
  components: {
    moduleName,
  },
  props: {
    title: String,
    link: String,
    myData: Object,
    windowSize: Object,
    chartId: String,
  },
  data() {
    return {
      topFive: [],
    };
  },
  watch: {
    "myData.companys": {
      handler(val) {
        this.$nextTick(() => {
          if (val) {
            let names = [],
              datas = [];
            for (let v of val) {
              names.push(v.name);
              datas.push(Number(v.num));
            }
            // console.info(names,datas);
            this.setChart(names, datas);
          }
        });
      },
      immediate: true,
    },
  },
  methods: {
    setChart(names, datas) {
      const chart = this.$echarts.init(document.getElementById(this.chartId));
      const option = {
        color: ["#009AFB"],
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow", // 默认为直线，可选为：'line' | 'shadow'
          },
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          top:0,
          containLabel: true,
        },
        xAxis: [
          {
            type: "value",
          },
        ],
        yAxis: [
          {
            type: "category",
            data: names,
            axisTick: {
              alignWithLabel: true,
            },
            axisLabel: {
              formatter: function(value) {
                return value.slice(0, 2);
              },
              fontSize: this.windowSize.height * 0.01,
              color: "rgba(0,0,0,0.5)",
            },
          },
        ],
        series: [
          {
            name: "",
            type: "bar",
            barWidth: this.windowSize.height * 0.01,
            data: datas,
          },
        ],
      };
      // console.log(chart);
      chart.setOption(option);
    },
  },
};
</script>

<style scoped>
.main {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 10vh;
}
.data {
  width: 10vw;
}
.member {
  font-size: 2.2vmin;
  font-weight: bold;
  color: #000000;
}
.member > span {
  font-size: 1.8vmin;
  color: rgba(0, 0, 0, 0.5);
  margin-right: 6px;
}
.chart {
  margin-top: 1vh;
  width: 100%;
  height: 12vh;
}
</style>
