<template>
  <div :class="`box ${topFive.length<5?'start':'between'}`">
    <div
      :class="`member ${item.done_rate < 60 ? 'red' : ''}`"
      v-for="(item,index) in topFive"
      :key="index"
    >
      <a-tooltip
        placement="top"
        arrow-point-at-center
        auto-adjust-overflow
        :title="item.name"
      >
        <p class="name" v-html="item.name.substr(0, 4)"></p>
        <p class="grade" v-if="typeof item.done_rate == 'number'">{{ item.done_rate }}%</p>
        <p class="grade" v-else>{{ item.floorcycle }}</p>
      </a-tooltip>
    </div>
  </div>
</template>

<script>
export default {
  name: "horizontalRank",
  props: {
    rank: Array,
  },
  watch: {
    rank: {
      handler(val) {
        const length = val.length;
        if (this.timer) clearInterval(this.timer);
        if (length > 0) {
          this.topFive = val.slice(0, 5);
          if (length > 5) {
            this.index = 0;
            this.timer = setInterval(() => {
              let index = this.index;
              if (++index * 5 >= length) {
                this.index = 0;
                this.topFive = val.slice(0, 5);
              } else {
                this.index = index;
                this.topFive = val.slice(index * 5, (1+index)*5);
              }
            }, 5000);
          }
        }
      },
      immediate: true,
    },
  },
  data() {
    return {
      topFive: [],
      index: 0,
    };
  },
};
</script>

<style scoped>
.box {
  display: flex;
  align-items: center;
}
.box.between{
  justify-content: space-between;
}
.member {
  max-width: 20%;
  text-align: center;
  cursor: default;
}
.box.start>.member{
  margin-right: 2vw;
}
.name {
  font-size: 2vmin;
  white-space: nowrap;
  overflow: hidden;
  /* text-overflow: ellipsis; */
  color: rgba(0, 0, 0, 0.85);
}
.grade {
  font-size: 1.8vmin;
  color: rgba(0, 0, 0, 0.5);
}
.member.red >>> .name,
.member.red >>> .grade {
  color: #e02020;
}
</style>
