<template>
  <div class="module" v-loading="!myData">
    <module-name :title="title" :link="link"></module-name>
    <template v-if="myData">
      <div class="main">
        <div class="square">
          <a-progress
            type="circle"
            :stroke-color="{
              '0%': '#00C4FF',
              '100%': '#009AFB',
            }"
            :width="windowSize.height * 0.1"
            :strokeWidth="14"
            :percent="myData.done_rate"
            gapPosition="bottom"
          >
            <template v-slot:format="percent">
              <p class="progress-percent">{{ percent }}<span>%</span></p>
            </template>
          </a-progress>
          <p class="title">月闭环率</p>
        </div>
        <div class="square">
          <a-progress
            type="circle"
            :stroke-color="{
              '0%': '#00C4FF',
              '100%': '#009AFB',
            }"
            :width="windowSize.height * 0.1"
            :strokeWidth="14"
            :percent="myData.all_done_rate"
            status="normal"
            gapPosition="bottom"
          >
            <template v-slot:format="percent">
              <p class="progress-percent">{{ percent }}<span>%</span></p>
            </template>
          </a-progress>
          <p class="title">累计闭环率</p>
        </div>
      </div>
      <div class="data">
        <div class="member">
          <p class="name">月度问题数</p>
          <p class="value">{{ myData.all_num }}</p>
        </div>
        <div class="member">
          <p class="name">月度闭环数</p>
          <p class="value">{{ myData.done_num }}</p>
        </div>
        <div class="member">
          <p class="name">累计闭环数</p>
          <p class="value">{{ myData.done_rate }}</p>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import moduleName from "@/components/moduleName";
export default {
  name: "windowEight",
  components: {
    moduleName,
  },
  props: {
    title: String,
    link: String,
    myData: Object,
    windowSize: Object,
  },
};
</script>

<style scoped>
.main {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1.6vh;
  margin-bottom: 1.2vh;
  /* height: 16vh; */
}
.main > .square > .title {
  font-size: 1.6vmin;
  color: rgba(0, 0, 0, 0.5);
  text-align: center;
  margin-top: 1.6vh;
}
.data {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}
.data > .member > .name {
  font-size: 2vmin;
  color: rgba(0, 0, 0, 0.85);
}
.data > .member > .value {
  font-size: 1.8vmin;
  color: rgba(0, 0, 0, 0.5);
}
</style>
