<template>
  <div class="module" v-loading="!myData">
    <template v-if="myData">
      <div class="main">
        <module-name :title="title" :link="link"></module-name>
        <div class="data">
          <p class="member"><span>分包数</span>{{ myData.num }}</p>
          <p class="member"><span>注册数</span>{{ myData.rnum }}</p>
        </div>
        <a-progress
          type="circle"
          :stroke-color="{
            '0%': '#00C4FF',
            '100%': '#009AFB',
          }"
          :width="windowSize.height * 0.1"
          :strokeWidth="14"
          :percent="myData.rate"
          status="normal"
          gapPosition="bottom"
        >
          <template v-slot:format="percent">
            <p class="progress-percent">{{ percent }}<span>%</span></p>
          </template>
        </a-progress>
      </div>
      <div class="list">
        <p class="title">未注册分包单位</p>
        <el-scrollbar :native="false" style="height: 20vh;">
        <ul class="companys">
          <li
            class="member"
            v-for="item in myData.list"
            :key="item.subcontractid"
          >
            <a-tooltip
              placement="topLeft"
              :title="item.subcontractname"
              arrow-point-at-center
            >
              <span v-text="item.subcontractname"></span>
            </a-tooltip>
            
          </li>
        </ul>
        </el-scrollbar>
      </div>
    </template>
  </div>
</template>

<script>
import moduleName from "@/components/moduleName";
export default {
  name: "windowTen",
  components: {
    moduleName
  },
  props: {
    title: String,
    link: String,
    myData: Object,
    windowSize: Object,
  },
  data() {
    return {
      topFive: [],
    };
  },
  watch: {
    "myData.company": {
      handler(val) {
        if (val) {
          this.topFive = val.slice(0, 5);
        }
      },
      immediate: true,
    },
  },
};
</script>

<style scoped>
.module {
  display: flex;
  justify-content: space-between;
}
.main > .data > .member {
  font-size: 2.2vmin;
  font-weight: bold;
  color: #000000;
}
.main > .data > .member > span {
  font-size: 1.8vmin;
  color: rgba(0, 0, 0, 0.5);
  margin-right: 6px;
}
.list {
  width: 50%;
}
.list > .title {
  font-size: 1.5vmin;
}
.list>>>.el-scrollbar__wrap {
  overflow-x: hidden;
}
.list .companys {
  width: 100%;
}
.list .companys > .member {
  font-size: 1.4vmin;
  padding: 2px 4px;
  border-radius: 4px;
  color: #000000;
  background-color: #eeeeee;
  margin-top: 1vh;
  white-space: nowrap;
  overflow: hidden;
  cursor: pointer;
}
</style>
